@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700&family=Roboto+Condensed:wght@700&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    @apply bg-orange-200;
  }
  h1 {
    @apply text-3xl font-bold;
  }
  h2 {
    @apply text-2xl font-bold;
  }
  h3 {
    @apply text-xl font-bold;
  }
}

@layer components {
  .button-primary {
    @apply bg-orange-500 border p-0.5;
  }
  .button-secondary {
    @apply bg-gray border p-0.5;
  }
}
